import { Chat } from "~/functions/transformChatData";

export default {
  name: "ExampleGraphs",
  data() {
    return {
      chat: undefined,
      linegraphHeaderChartOptions: {
        tooltips: { enabled: false },
        hover: { mode: null },
        pointHitRadius: 5,
        responsive: true,
        maintainAspectRatio: true,
        lineTension: 1,
        legend: {
          position: "top",
          labels: {
            fontStyle: "bold",
            fontColor: "rgb(51,51,51)",
            fontSize: 10
          }
        },
        scales: {
          xAxes: [
            {
              type: "time",
              time: {},
              gridLines: {
                display: false,
                color: "#FFFFFF"
              },
              ticks: {
                fontColor: "rgb(51,51,51)",
                fontStyle: "bold",
                fontSize: 11
              }
            }
          ],
          yAxes: [
            {
              scaleLabel: {
                display: true,
                labelString: this.$t("messages"),
                fontColor: "rgb(51,51,51)",
                fontStyle: "bold",
                fontSize: 11
              },
              ticks: {
                precision: 0,
                stepSize: 1,
                beginAtZero: true,
                display: false
              },
              gridLines: {
                display: false,
                color: "#FFFFFF"
              }
            }
          ]
        },
        elements: {
          line: {
            tension: 0
          }
        }
      },
      donoughtHeaderChartOptions: {
        tooltips: { enabled: true },
        hover: { mode: null },
        responsive: true,
        maintainAspectRatio: true,
        legend: {
          position: "bottom",
          labels: {
            fontStyle: "bold",
            fontColor: "rgb(51,51,51)",
            fontSize: 10
          }
        },
        scales: {
          yAxes: [
            {
              scaleLabel: {
                display: false,
                labelString: this.$t("messages"),
                fontColor: "rgb(51,51,51)",
                fontStyle: "bold",
                fontSize: 11
              },
              ticks: {
                display: false
              },
              gridLines: {
                display: false
              }
            }
          ]
        }
      },
      radarchartHeaderChartOptions: {
        tooltips: { enabled: false },
        hover: { mode: null },
        responsive: true,
        maintainAspectRatio: true,
        scale: {
          angleLines: {
            // display: false,
            color: "rgb(51,51,51)"
          },
          ticks: {
            display: false
          },
          gridLines: {
            color: "rgb(51,51,51)"
          },

          pointLabels: {
            fontColor: "rgb(51,51,51)"
          }
        },
        legend: {
          position: "top",
          labels: {
            fontStyle: "bold",
            fontColor: "rgb(51,51,51)",
            fontSize: 10
          }
        }
      },
      barchartHeaderChartOptions: {
        tooltips: { enabled: true },
        hover: { mode: null },
        responsive: true,
        maintainAspectRatio: true,
        legend: {
          position: "bottom",
          labels: {
            fontStyle: "bold",
            fontColor: "rgb(51,51,51)",
            fontSize: 10
          }
        },
        scales: {
          xAxes: [
            {
              gridLines: {
                display: false
              },
              ticks: {
                fontColor: "rgb(51,51,51)"
              }
            }
          ],
          yAxes: [
            {
              scaleLabel: {
                display: false,
                labelString: this.$t("messages"),
                fontStyle: "bold",
                fontColor: "rgb(51,51,51)",
                fontSize: 10
              },
              gridLines: {
                display: false
              },
              ticks: {
                beginAtZero: true,
                precision: 0,
                display: false
              }
            }
          ]
        }
      }
    };
  },
  created() {
    // eslint-disable-next-line no-undef
    if (process.client) {
      fetch("/example-results.json")
        .then((response) => response.text())
        .then((messages) => {
          var instance = new Chat();
          var serializedObject = JSON.parse(messages);
          Object.assign(instance, {
            _lineGraphData: Promise.resolve(serializedObject[0]),
            _funfacts: Promise.resolve(serializedObject[1]),
            _allWords: Promise.resolve(serializedObject[2]),
            _hourlyData: Promise.resolve(serializedObject[3]),
            _dailyData: Promise.resolve(serializedObject[4]),
            _weeklyData: Promise.resolve(serializedObject[5]),
            _shareOfSpeech: Promise.resolve(serializedObject[6])
          });
          this.chat = instance;
        });
    }
  }
};
